<template>
    <v-container>
      <v-hover v-slot:default="{hover}">
      <v-row no-gutters >
        <v-col 
          key="1" cols="1"
          sm="12" align="center" justify="center"
        >
          <v-card flat
            class="pa-2"
          >
          <v-btn
            v-ripple
            :elevation="hover ? 16 : 2"
            v-if="!testStarted"
            v-on:click="start"
            fab
            class="primary" 
            dark
            x-large
          >Start</v-btn>
          <!-- Flashcard section -->
          <div v-if="testStarted">
              <div class="ma-2">
                <strong class="mx-4 info--text text--darken-2">Remaining: {{questionsRemaining}}</strong>
                <v-divider vertical></v-divider>
                <strong class="mx-4 success--text text--darken-2">Completed: {{questionNumber - 1}}</strong>
              </div>
              <v-card max-width="300" :elevation="hover ? 16 : 2">
                <v-progress-linear class="textLeftAlign" rounded v-model="question.progress"></v-progress-linear>
                <v-row justify="center" align="center">
                  <v-col>
                    <h2 class="font-weight-bold display-3 pa-4">{{question.question}}</h2>
                  </v-col>

                  <v-col cols="auto" class="text-center pl-0">
                    <v-row class="flex-column ma-0 fill-height" justify="center">
                      <v-col class="px-o" v-for="item in question.hints" :key="Math.random()">
                        <v-btn @click="checkAnswer(item)" fab dark color="secondary">{{item}}</v-btn>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
              </v-card>
          </div>          
          <!-- Flashcard section -->

          <!-- You are done message -->
          <v-dialog
            v-model="testEndDialog"
            max-width="400"
          >
            <v-card>
              <v-card-title class="headline">Your grade {{testGrade}}% or {{testScore}} out of {{totalQuestions}} </v-card-title>
      
              <v-card-text>
                If you want to see more details click on test statistics
              </v-card-text>
      
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="closeDialog">Close</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>          

          </v-card>
        </v-col>
      </v-row>
      </v-hover>
    </v-container>
</template>

<script>
export default {
  name: "Flashcard",
  props: {
    question: {
      type: Object,
      default: function() {
        return {
          question: "",
          hints: []
        };
      }
    }
  },
  data: function () {
    return {
    }
  },
  computed: {
    questionsRemaining() {
      var c =
        this.$store.state.settings.questions -
        this.$store.state.questionCounter;
      return c > 0 ? c : 0;
    },
    questionNumber() {
      return this.$store.state.questionCounter;
    },
    totalQuestions() {
      return this.$store.state.settings.questions;
    },
    testStarted() {
      return this.$store.state.testStarted;
    },
    testEnded() {
      return this.$store.state.testEnded;
    },
    testEndDialog(){
      return this.$store.state.testEndDialog;
    },
    testScore() {
      return this.$store.state.score;
    },
    testGrade() {
      return this.$store.state.testGrade;
    },
    wrongAnswers() {
      return this.totalQuestions - this.testScore;
    }
  },
  methods: {
    start: function() {
      this.$store.dispatch("testStarted");
    },
    checkAnswer: function(elm) {
      this.$store.dispatch("checkAnswer", elm);
    },
    closeDialog: function(){
      this.$store.state.testEndDialog = false;
    }
  }
};
</script>
<style scoped>
  .textLeftAlign{
    text-align: left;
  }
</style>